import { EventTarget } from "../events/eventTarget";

const TOPIC_IDENTITIES_CHANGE = "onIdentitiesChange";

export class IdentityEvents {
  constructor() {
    this.onIdentitiesChangeEventTarget = document; // we want this to be document target , so others can listen for this event
    this.previousValue = null;
  }

  trigger(identities) {
    const hasChanged =
      !this.previousValue ||
      JSON.stringify(this.previousValue) !== JSON.stringify(identities);
    if (hasChanged) {
      this.previousValue = identities;
      // publish that identities have changed
      this.onIdentitiesChangeEventTarget.dispatchEvent(
        new CustomEvent(TOPIC_IDENTITIES_CHANGE, { detail: identities })
      );
    }
  }

  /**
   * Listens to changes in identities and invokes the provided callback function.
   *
   * @param {Function} callback - The callback function to be invoked when identities change.
   */
  listen(callback) {
    this.onIdentitiesChangeEventTarget.addEventListener(
      TOPIC_IDENTITIES_CHANGE,
      callback
    );
  }
  reset() {
    this.previousValue = null;
  }

  /**
   * Removes the listener for identities change event.
   *
   * @param {Function} callback - The callback function to be removed.
   */
  removeListener(callback) {
    this.onIdentitiesChangeEventTarget.removeEventListener(
      TOPIC_IDENTITIES_CHANGE,
      callback
    );
  }
}
