import { RequestService } from "../api";
import { interactServerEndpoint } from "../constant";
import { IdentityService } from "../ID/identity";
import { LoggerService } from "../logger";
import { OptionsService } from "../options";
import { PageService } from "../page/page";
import { InteractionLoggingService } from "./interactionLogging";
import { flattenObject } from "./utils/util";
import { DisplayService } from "./utils/display";
/**
 * Represents a service for handling interactions.
 */
export class InteractionsService {
  /**
   * Creates an instance of InteractionsService.
   * @param {OptionsService} optionsService - The options service.
   * @param {LoggerService} logger - The logger.
   * @param {RequestService} requestService - The request service.
   * @param {PageService} pageService - The page service.
   * @param {Window} window - The window object.
   * @param {IdentityService} identityService - The identity service.
   */
  constructor(
    optionsService,
    logger,
    requestService,
    pageService,
    window,
    identityService
  ) {
    this.optionsService = optionsService;
    this.logger = logger;
    this.requestService = requestService;
    this.pageService = pageService;
    this.window = window;
    this.identityService = identityService;
    this.localStorage = window.localStorage;
    // this.cookieStorage = cookieStorage;
    this.sessionStorage = window.sessionStorage;
    this.interactionLoggingService = new InteractionLoggingService(
      optionsService,
      logger,
      requestService
    );
    this.displayService = new DisplayService(
      window,
      this.interactionLoggingService
    );
  }

  /**
   * Fetches interactions.
   * @returns {Promise} A promise that resolves with the fetched interactions.
   */
  async fetchInteractions() {
    // TODO: Make options driven
    const identities = flattenObject(this.identityService.getAllIdentites());
    const interactionEndpoint = interactServerEndpoint;
    const url = `${interactionEndpoint}/interact/interactions`; // TODO: Get this from options
    const response = await new Promise((resolve, reject) => {
      this.requestService.post(
        url,
        {
          body: {
            ...identities,
            ...this.pageService.getPageProperties(),
          },
        },
        { basicAuthKey: btoa("w_k:" + this.optionsService.options.apiKey) },
        (status, response) => {
          // Handle the response here
          if (status >= 200 && status < 300) {
            const res = JSON.parse(response);
            resolve(res.interactions || []);
          } else {
            reject(new Error("Failed to fetch interactions"));
          }
        }
      );
    });
    return response;
  }

  /**
   * Performs an event interaction.
   * @param {SDKInteraction} interaction - The interaction to be performed.
   */
  doEventInteraction(interaction) {
    // TODO: Replace with logging library
    this.window.dispatchEvent(
      new CustomEvent(interaction.eventName, { detail: interaction.data })
    );
    return true;
  }

  spliceValueFromData(data, key) {
    const value = data[key];
    delete data[key];
    return value;
  }

  /**
   * Performs a function interaction.
   * @param {SDKInteraction} interaction - The interaction to be performed.
   */
  doFunctionInteraction(interaction) {
    const data = { ...interaction.data };

    switch (interaction.functionName) {
      case "setLocalStorage":
        var localStorageKey = this.spliceValueFromData(data, "localStorageKey");
        this.localStorage.setItem(localStorageKey, JSON.stringify(data));
        return true;

      case "setSessionStorage":
        const sessionStorageKey = this.spliceValueFromData(
          data,
          "sessionStorageKey"
        );
        this.sessionStorage.setItem(sessionStorageKey, JSON.stringify(data));
        return true;
      case "setGlobalVariable":
        const globalVariableKey = this.spliceValueFromData(
          data,
          "globalVariableName"
        );
        this.window[globalVariableKey] = data;
        return true;
      case "globalCallback":
        const callbackName = this.spliceValueFromData(data, "callbackName");
        if (!!this.window[callbackName]) {
          this.window[callbackName](data);
          return true;
        }
        return false;
      default:
        if (this.window[interaction.functionName]) {
          this.window[interaction.functionName](data);
          return true;
        }
    }
    return false;
  }

  doDataInteraction(interaction) {
    const data = { ...interaction.data };

    const dataToSendConfigDetails = interaction?.dataToSendConfigDetails || {};
    if (!dataToSendConfigDetails.functionName) return false;
    switch (dataToSendConfigDetails.functionName) {
      case "setLocalStorage":
        var localStorageKey = dataToSendConfigDetails?.storageKey;
        this.localStorage.setItem(localStorageKey, JSON.stringify(data));
        return true;
      case "setSessionStorage":
        const sessionStorageKey = dataToSendConfigDetails?.storageKey;
        this.sessionStorage.setItem(sessionStorageKey, JSON.stringify(data));
        return true;
      case "setGlobalVariable":
        const globalVariableKey = dataToSendConfigDetails?.variableName;
        this.window[globalVariableKey] = data;
        return true;
      case "globalCallback":
        const callbackName = dataToSendConfigDetails?.callbackName;
        if (!!this.window[callbackName]) {
          this.window[callbackName](data);
          return true;
        }
        return false;
      default:
        if (this.window[interaction.functionName]) {
          this.window[interaction.functionName](data);
          return true;
        }
    }
    return false;
  }

  performDataInteractions(interactions = []) {
    if (!Array.isArray(interactions)) return [];

    const legacyInteractions = interactions.filter(
      (interaction) => interaction.type !== "data"
    );
    const dataInteractions = interactions.filter(
      (interaction) => interaction.type === "data"
    );

    legacyInteractions.forEach((interaction) => {
      switch (interaction.type) {
        // TODO: Keeping these for existing interactions , remove after migration
        case "event":
          this.callPixelOnDone(interaction)(() =>
            this.doEventInteraction(interaction)
          );
          break;
        case "function":
          this.callPixelOnDone(interaction)(() =>
            this.doFunctionInteraction(interaction)
          );
          break;
        default:
          break;
      }
    });

    dataInteractions.forEach((interaction) => {
      this.callPixelOnDone(interaction)(() =>
        this.doDataInteraction(interaction)
      );
    });
  }

  performDisplayInteractions(interactions = []) {
    if (!Array.isArray(interactions)) return [];
    const displayInteractions =
      this.displayService.pickFirstDisplayInteractionOfEachType(interactions);
    displayInteractions.forEach((interaction) => {
      this.callPixelOnDone(interaction)(() =>
        this.displayService.performDisplayInteraction(interaction)
      );
    });
  }

  /**
   * Performs interactions using the provided list of SDKInteraction objects.
   * @param {SDKInteraction[]} interactions - The list of SDKInteraction objects.
   */
  performInteractions(interactions = []) {
    // TODO: Implement interaction logic here
    if (!Array.isArray(interactions)) return [];

    const displayInteractions = interactions.filter(
      (interaction) => interaction.type === "display"
    );
    const dataInteractions = interactions.filter(
      (interaction) => interaction.type !== "display"
    );
    this.performDataInteractions(dataInteractions);
    this.performDisplayInteractions(displayInteractions);
  }

  callPixelOnDone(interaction) {
    return (fn) => {
      if (fn()) {
        this.interactionLoggingService.callInteractionPixel(
          interaction,
          "onPerform"
        );
      }
    };
  }

  async publishOnRecieve(interactions = []) {
    if (!Array.isArray(interactions)) return [];
    interactions.forEach((interaction) => {
      this.interactionLoggingService.callInteractionPixel(
        interaction,
        "onRecieve"
      );
    });
  }

  /**
   * Resolves interactions by fetching them from the interactions service.
   */
  async resolveInteractions() {
    try {
      const interactions = await this.fetchInteractions();

      await this.publishOnRecieve(interactions);

      // TODO: Implement interaction resolution for delayed interactions here (if needed)
      this.performInteractions(interactions);
    } catch (error) {
      console.error(error);
    }
  }
}
