import { TEST_COOKIE } from "../constant";
import { OptionsService, StorageOptions } from "../options";
import utils from "../utils";

export const Storage = (Cookie, localStorage) => {
  var cookieStorage = new Cookie();
  var isCookieEnabled = function () {
    var uid = String(new Date());
    var result;
    try {
      cookieStorage.set(TEST_COOKIE, uid);
      result = cookieStorage.get(TEST_COOKIE) === uid;
      cookieStorage.remove(TEST_COOKIE);
      return result;
    } catch (e) {
      // cookies are not enabled
    }
    return false;
  };
  if (isCookieEnabled()) return cookieStorage;
  return localStorage;
};

/**
 * @typedef {object} Storage
 * @property {Function} get
 * @property {Function} set
 * @property {Function} remove
 */
/**
 * @class Service to handle storage related tasks
 *
 */
export class StorageService {
  /**
   *
   * @param {OptionsService} optionsService
   * @param {Storage} cookie
   * @param {Storage} localStorage
   */
  constructor(optionsService, cookie, localStorage) {
    this.cookie = cookie;
    this.localStorage = localStorage;
    this.optionsService = optionsService;
    this.tld = this.cookie.getTld();
    this.optionsService.listenToOptionsChange(() => {
      var opts = this.optionsService.getStorageOptions();
      this.options = opts;
      this.options.domain = this.cookie.getValidDomain(opts);
    });
    // domain can be tld or domain
  }

  get isCookieEnabledInOptions() {
    return this.optionsService._options.persistenceInCookieStorage;
  }

  isCookieEnabled() {
    if (this.isCookieEnabledValue !== undefined)
      return this.isCookieEnabledValue;
    var uid = String(new Date());
    var result;
    try {
      // TODO : Verify if this is the right way to check cookie enabled
      this.cookie.set(TEST_COOKIE, uid, { domain: this.tld });
      result = this.cookie.get(TEST_COOKIE, { domain: this.tld }) === uid;
      this.cookie.remove(TEST_COOKIE);
      this.isCookieEnabledValue = result;
      return result;
    } catch (e) {
      // cookies are not enabled
      this.isCookieEnabledValue = false;
    }
    this.isCookieEnabledValue = false;
    return false;
  }
  /**
   * @returns {Storage}
   */
  getStorage(explicitlyLocal) {
    return explicitlyLocal
      ? this.localStorage
      : this.isCookieEnabled()
      ? this.cookie
      : this.localStorage;
  }
  set(name, value, overrideOptions = {}, explicitlyLocal = false) {
    var opts = { ...this.options, ...overrideOptions };
    this.getStorage(explicitlyLocal).set(name, value, {
      ...opts,
      domain: opts.domain ? opts.domain : this.tld,
    });
  }
  /**
   *
   * @param {string} name
   * @param {StorageOptions} overrideOptions
   */
  get(name, overrideOptions = {}, explicitlyLocal = false) {
    var opts = { ...this.options, ...overrideOptions };
    return this.getStorage(explicitlyLocal).get(name, {
      ...opts,
      domain: !!opts.domain ? opts.domain : this.tld,
    });
  }
  remove(name, overrideOptions, explicitlyLocal = false) {
    var opts = { ...this.options, ...overrideOptions };
    return this.getStorage(explicitlyLocal).remove(name, {
      ...opts,
      domain: !!opts.domain ? opts.domain : this.tld,
    });
  }

  /**
   *
   * @param {string} name - key name of the storing pair.
   * @param {string} value - value of storing pair.
   * @param {object} overrideOptions - hash -> whether to store the value in SHA256 encrypted format or raw. isString -> whether the incoming value is string or not. if not, we stringify the input
   * @returns
   */
  setSessionStorage(name, value, overrideOptions = {}) {
    if (value == null) {
      return;
    }
    var opts = { ...this.options };
    var _name = utils.domainSpecific(name, {
      ...opts,
      domain: opts.domain ? opts.domain : this.tld,
    });
    if (overrideOptions.hash) {
      var hashString;
      if (overrideOptions.isString) {
        hashString = utils.hash256(value);
      } else {
        hashString = utils.hash256(JSON.stringify(value));
      }
      sessionStorage.setItem(_name, utils.base64.encode(hashString));
    } else {
      var _value;
      if (overrideOptions.isString) {
        _value = value;
      } else {
        _value = JSON.stringify(value);
      }
      sessionStorage.setItem(_name, utils.base64.encode(_value));
    }
  }

  /**
   *
   * @param {string} name -  key name of the storing pair.
   * @param {object} overrideOptions - isString -> whether the stored value is intented to be string or object. Hashed values are always stored as strings.
   * @returns
   */
  getSessionStorage(name, overrideOptions = {}) {
    var opts = { ...this.options };
    var _name = utils.domainSpecific(name, {
      ...opts,
      domain: opts.domain ? opts.domain : this.tld,
    });
    var value = sessionStorage.getItem(_name);
    try {
      if (value) {
        if (overrideOptions.isString || overrideOptions.hash) {
          return utils.base64.decode(value);
        } else {
          return JSON.parse(utils.base64.decode(value));
        }
      }
    } catch (e) {
      return null;
    }
  }

  removeSessionStorage(name) {
    var opts = { ...this.options };
    var _name = utils.domainSpecific(name, {
      ...opts,
      domain: opts.domain ? opts.domain : this.tld,
    });
    sessionStorage.removeItem(_name);
  }

  getFromCookieOrSession(name, overrideOptions = {}) {
    var opts = { ...this.options, ...overrideOptions };
    return this.isCookieEnabledInOptions && this.isCookieEnabled()
      ? this.cookie.get(name, {
          ...opts,
          domain: !!opts.domain ? opts.domain : this.tld,
        })
      : this.getSessionStorage(name, overrideOptions);
  }

  setInCookieOrSession(name, value, overrideOptions = {}) {
    var opts = { ...this.options, ...overrideOptions };
    this.isCookieEnabledInOptions && this.isCookieEnabled()
      ? this.cookie.set(name, value, {
          ...opts,
          domain: opts.domain ? opts.domain : this.tld,
        })
      : this.setSessionStorage(name, value, overrideOptions);
  }

  removeFromCookieOrSession(name, overrideOptions) {
    var opts = { ...this.options, ...overrideOptions };
    this.isCookieEnabledInOptions && this.isCookieEnabled()
      ? this.cookie.remove(name, {
          ...opts,
          domain: !!opts.domain ? opts.domain : this.tld,
        })
      : this.removeSessionStorage(name);
  }
}
