
 /**
  * Event types that are translated 
  * 
  * @param {string} type 
  * @param {any} data 
  */
const Event = function(type,data){
    this.type = type;
    this.data = data;
}

/**
 * A unique target for events that can be published against topics 
 */
var EventTarget = function() {
    this.listeners = {};
  };
  EventTarget.prototype.listeners = null;

  /**
    * @callback onEventRecievedCallback
    * @param {Event} event
  */
  /**
   * 
   * @param {string} type - event type you want to subscribe to  
   * @param {onEventRecievedCallback} callback 
   */
  EventTarget.prototype.addEventListener = function(type, callback) {
    if (!(type in this.listeners)) {
      this.listeners[type] = [];
    }
    this.listeners[type].push(callback);
  };
  
  EventTarget.prototype.removeEventListener = function(type, callback) {
    if (!(type in this.listeners)) {
      return;
    }
    var stack = this.listeners[type];
    for (var i = 0, l = stack.length; i < l; i++) {
      if (stack[i] === callback){
        stack.splice(i, 1);
        return;
      }
    }
  };
  EventTarget.prototype.removeAll = function(_type=null){
    var types = Object.keys(this.listeners)
    if(_type){
      this.listeners[_type] = []
    }else{
      for(var type of types){
        this.listeners[type] = []
      }
    }
  }
  
  EventTarget.prototype.dispatchEvent = function(event) {
    if (!(event.type in this.listeners)) {
      return true;
    }
    var stack = this.listeners[event.type].slice();
  
    for (var i = 0, l = stack.length; i < l; i++) {
      stack[i].call(this, event);
    }
    return !event.defaultPrevented;
  };

  export { EventTarget, Event };
