
export default (doc)=>({
     getCookie : (name) => {
        try {
          var ca = doc.cookie.split(';');
          var value = null;
          for (var i = 0; i < ca.length; i++) {
            var c = ca[i];
            while (c.charAt(0) === ' ') {
              c = c.substring(1, c.length);
            }
            if (c.indexOf(name) === 0) {
              value = c.substring(name.length, c.length);
              break;
            }
          }
          return value;
        } catch (e) {
          return null;
        }
      },
    setCookie : (name, value, opts) => {
      var expires = value !== null ? opts.expirationDateTime : -1 ;
      if (expires) {
        var date = new Date();
        //date.getTime() + (expires * 24 * 60 * 60 * 1000)
        date.setTime(expires);
        expires = date;
      }        
      var str = name + '=' + value;
        if (expires) {
          str += '; expires=' + expires.toUTCString();
        }
        str += '; path=/';
        if (opts.domain) {
            // if unset this will set the host of the current document location, excluding subdomains. 
          str += '; domain=' + opts.domain;
        }
        if (opts.secure) {
          str += '; Secure';
        }
        str +=`; samesite=${opts.sameSite}`
        doc.cookie = str;
      }    
})
