import { InteractionLoggingService } from "../interactionLogging";

export const displayInteractionTypes = ["sticky-banner", "full-screen-overlay"];
export const displayInteractionsOrder = [
  "sticky-banner",
  "full-screen-overlay",
];

export class DisplayService {
  /**
   *
   * @param {Window} window - The window object.
   * @param {InteractionLoggingService} interactionLoggingService - The interaction logging service.
   */
  constructor(window, interactionLoggingService) {
    this.document = window.document;
    this.interactionLoggingService = interactionLoggingService;
  }

  logInteractionDismissed(interaction) {
    this.interactionLoggingService.callInteractionPixel(
      interaction,
      "onDismiss"
    );
  }

  logInteractionClicked(interaction) {
    this.interactionLoggingService.callInteractionPixel(interaction, "onClick");
  }

  applyStyleToElement(div, style) {
    Object.keys(style).forEach((key) => {
      div.style[key] = style[key];
    });
  }

  createDiv(style, cssClass = "interactDiv") {
    const div = this.document.createElement("div");
    div.classList.add(cssClass);
    this.applyStyleToElement(div, style);
    return div;
  }

  appendDivToBody(div) {
    this.document.body.appendChild(div);
  }

  setDisplayProperty(div, value) {
    div.style.display = value;
  }

  hideDiv(div) {
    this.setDisplayProperty(div, "none");
  }

  removeDiv(div) {
    div.remove();
  }

  addCloseButtonToDivTopRight(interaction, div) {
    const closeButton = this.document.createElement("button");
    div.style.display = "relative";
    closeButton.textContent = "X";
    const style = {
      background: "transparent",
      border: "unset",
      cursor: "pointer",
      position: "absolute",
      top: "0",
      right: "0",
    };
    this.applyStyleToElement(closeButton, style);

    closeButton.addEventListener(
      "click",
      function () {
        this.logInteractionDismissed(interaction);
        this.removeDiv(div);
      }.bind(this)
    );
    div.appendChild(closeButton);
  }

  applyClickOutsideToClose(interaction, div) {
    const listener = this.document.addEventListener(
      "click",
      function (event) {
        if (!div.contains(event.target)) {
          this.logInteractionDismissed(interaction);
          this.removeDiv(div);
          this.document.removeEventListener("click", listener);
        }
      }.bind(this)
    );
  }

  redirect(url) {
    window.open(url, "_blank");
  }

  applyIframeClickRedirect(interaction, iframe, redirectUrl) {
    window.addEventListener(
      "message",
      (e) => {
        const key = e.message ? "message" : "data";
        const data = e[key];
        if (data === "zeoFrameClick") {
          this.logInteractionClicked(interaction);
          this.redirect(redirectUrl);
          this.removeDiv(iframe);
        }
      },
      false
    );
  }

  getStyleForPosition(config = "") {
    switch (config) {
      case "center":
        return {
          position: "absolute",
          top: "50%",
          left: "50%",
          transform: "translate(-50%, -50%)",
        };
      case "bottom":
        return {
          position: "absolute",
          bottom: "0",
          left: "0",
        };
      default:
        return {};
    }
  }

  getStyleFromSizeDetails(sizeDetails = {}) {
    const { width, height } = sizeDetails;
    return {
      width: width ? `${width}px` : "fit-content",
      height: height ? `${height}px` : "fit-content",
    };
  }
  addImageWithAnchor(interaction, div, options) {
    const { imageUrl, anchorUrl } = options;
    const openInNewPage = interaction?.configDetails?.openInNewPage ?? false;
    const anchor = this.document.createElement("a");
    anchor.href = anchorUrl;

    anchor.target = openInNewPage ? "_blank" : "_self";
    anchor.addEventListener("click", function () {
      this.logInteractionClicked(interaction);
      this.removeDiv(div);
    });
    const image = this.document.createElement("img");
    image.src = imageUrl;
    anchor.appendChild(image);
    div.appendChild(anchor);
  }

  appendIframeToDiv(interaction, div, src, redirectUrl = "#") {
    const iframe = this.document.createElement("iframe");
    iframe.src = src;
    iframe.style.width = "100%";
    iframe.style.height = "100%";
    iframe.style.border = "unset";
    iframe.onload = function () {
      this.applyIframeClickRedirect(interaction, div, redirectUrl);
    }.bind(this);
    div.appendChild(iframe);
    return iframe;
  }

  displayOverlayInteraction(interaction) {
    // things that remain static
    const positionStyle = this.getStyleForPosition("center");
    const style = {
      "z-index": "9999999", // max possible value
    };
    // no tint for overlay
    // all overlay is centred
    const config = interaction?.displayConfigDetails || {};
    const configStyle = config.style || {};
    const redirectUrl = configDetails.redirectUrl || "";
    const tagType = configDetails.tagType || "";
    const src = configDetails.src || "";

    const baseDivStyle = {
      position: "fixed",
      top: "0",
      left: "0",
      width: "100%",
      height: "100%",
      background: "rgba(0, 0, 0, 0.7)" /* Semi-transparent black background */,
      display: "flex",
      "justify-content": "center",
      "align-items": "center",
      "z-index": "1000" /* Ensure it's on top */,
    };

    const contentDivStyle = {
      borderRadius: "8px",
      padding: "2%" /* Adjust padding based on percentage */,
      boxShadow: "0 4px 8px rgba(0, 0, 0, 0.2)",
      ...configStyle,
    };

    const baseDiv = this.createDiv(baseDivStyle, "overlayDiv");
    const contentDiv = this.createDiv(contentDivStyle, "overlayContentDiv");
    this.addCloseButtonToDivTopRight(interaction, contentDiv);
    if (tagType === "image") {
      this.addImageWithAnchor(interaction, contentDiv, {
        imageUrl: src,
        anchorUrl: redirectUrl,
      });
    } else {
      this.appendIframeToDiv(interaction, contentDiv, src, redirectUrl);
    }

    baseDiv.appendChild(contentDiv);
    contentDiv.remove = () => {
      baseDiv.remove();
    };
    this.appendDivToBody(baseDiv);
  }

  displayStickyBanner(interaction) {}

  displayPopupInteraction(interaction) {
    const configDetails = interaction?.configDetails || {};
    const positionDetails = this.getStyleForPosition(configDetails.position);
    const sizeDetails = configDetails.sizeDetails || {};
    const redirectUrl = configDetails.redirectUrl || "";
    const tagType = configDetails.tagType || "";
    const src = configDetails.src || "";
    const tintBackgroundForPopup =
      configDetails.tintBackgroundForPopup || false;
    const tintBackgroundColor =
      configDetails.tintBackgroundColor || "rgba(0,0,0,0.5)";
    const defaultPopupStyle = {
      "z-index": "9999999", // max possible value
    };
    const tintDiv = this.createDiv(
      {
        position: "fixed",
        top: "0",
        left: "0",
        width: "100%",
        height: "100%",
        background: tintBackgroundColor, // this should be taken from config
        "z-index": "9999998",
      },
      "containerDiv"
    );

    if (src) {
      const testDiv = this.createDiv({
        ...defaultPopupStyle,
        ...positionDetails,
        ...sizeDetails,
      });

      this.addCloseButtonToDivTopRight(interaction, testDiv);
      // this.applyClickOutsideToClose(interaction, testDiv);
      if (tagType === "image") {
        this.addImageWithAnchor(interaction, testDiv, {
          imageUrl: src,
          anchorUrl: redirectUrl,
        });
      } else {
        this.appendIframeToDiv(interaction, testDiv, src, redirectUrl);
      }
      if (tintBackgroundForPopup) {
        tintDiv.appendChild(testDiv);
        testDiv.remove = () => {
          tintDiv.remove();
        };
        this.appendDivToBody(tintDiv);
      } else {
        this.appendDivToBody(testDiv);
      }
      // const iframe =
    }
  }

  filterDisplayInteractions(interactions = []) {
    return interactions.filter((interaction) =>
      displayInteractionTypes.includes(
        interaction.displayConfigDetails.displayType
      )
    );
  }

  pickFirstDisplayInteractionOfEachType(interactions = []) {
    const displayInteractions = this.filterDisplayInteractions(interactions);
    const displayInteractionsMap = displayInteractions.reduce(
      (acc, interaction) => {
        const key = interaction.displayConfigDetails.displayType;
        if (!acc[key]) {
          acc[key] = interaction;
        }
        return acc;
      },
      {}
    );
    return displayInteractionsOrder
      .map((type) => displayInteractionsMap[type])
      .filter(Boolean);
  }

  performDisplayInteraction(interaction) {
    const displayConfigDetails = interaction?.displayConfigDetails || {};

    const { displayType } = displayConfigDetails;

    switch (displayType) {
      case "sticky-banner":
        this.displayStickyBanner(interaction);
        return true;
        break;
      case "full-screen-overlay":
        this.displayService.displayOverlayInteraction(interaction);
        return true;
        break;
      default:
        return false;
    }
  }
}
