var getIdentitesSPLEventName = function getIdentitesSPLEventName(
  existingIds,
  identities,
  isHashed = false
) {
  for (let key in identities) {
    if (!existingIds[key]) {
      return SPL_EVENT_NAMES.SET_USER_IDENTITIES;
    } else if (!!existingIds[key]) {
      if (VALID_ID_KEYS.indexOf(key) >= 0) {
        if (isHashed) {
          var hashToCheck = !!existingIds[key]["sha256"]
            ? "sha256"
            : "sha256_lowercase";
          if (existingIds[key][hashToCheck] !== identities[key][hashToCheck]) {
            return SPL_EVENT_NAMES.UPDATE_USER_IDENTITIES;
          }
        } else {
          if (existingIds[key] !== identities[key]) {
            return SPL_EVENT_NAMES.UPDATE_USER_IDENTITIES;
          }
        }
      } else {
        if (existingIds[key] !== identities[key]) {
          return SPL_EVENT_NAMES.UPDATE_USER_IDENTITIES;
        }
      }
    }
  }
  return null;
};

var sanitizeEmail = function sanitizeEmail(email) {
  // Split the email into local part and domain part
  let [localPart, domain] = email.split('@');
  
  // Remove + and . from the local part
  localPart = localPart.replace(/[+.]/g, '');
  
  // Reconstruct the email with the sanitized local part and the original domain
  return `${localPart}@${domain}`;
}

