import { GA_COOKIE_SUFFIX, PERSISTENT_STORAGE_KEY_PREFIX } from "../constant";
import { LoggerService } from "../logger";
import { OptionsService } from "../options";
import { StorageService } from "../storage/storage";
import utils from "../utils";
import { IdentityEvents } from "./identityEvents";
export const KEY_ZI = "zi";
export const KEY_IDENTITY = "identity";
export const ZSID_KEY = "_zsid";
export const KEY_ZS = "zs";

export const GA_COOKIE_CONFIG = {
  ignoreSuffix: true,
  domain: null,
  encrypted: false,
};

export const getZIKey = (apiKey) => {
  return (
    PERSISTENT_STORAGE_KEY_PREFIX + "_" + utils.base64.encode(apiKey) + KEY_ZI
  );
};
export const getIdentityKey = (apiKey) => {
  return (
    PERSISTENT_STORAGE_KEY_PREFIX +
    "_" +
    utils.base64.encode(apiKey) +
    KEY_IDENTITY
  );
};
export const getZSKey = (apiKey) => {
  return (
    PERSISTENT_STORAGE_KEY_PREFIX + "_" + utils.base64.encode(apiKey) + KEY_ZS
  );
};

/**
 * @typedef {object} Identity
 * @property {string} [email]
 * @property {string} [cellno]
 * @property {string} [loginid]
 */

/**
 * Service to manage storage,retrival and usage of identities
 *
 * @class - Service class to deal with creating and generating Zeotap publisher IDs
 */
export class IdentityService {
  /**
   *
   * @param {OptionsService} optionsService - options service handler
   * @param {StorageService} storageService - storage service
   * @param {LoggerService} logger - logger util
   */
  constructor(optionsService, storageService, logger) {
    this.optionsService = optionsService;
    this.storageService = storageService;
    this.logger = logger;
    this._zi = null;
    this._zs = null;
    this._identity = {};
    this._gaClientId = null;
    this._gaUserId = null;
    this._identityEvents = new IdentityEvents();
  }
  /**
   * check if zi is present ( if not generate a guuid) and return the same
   *
   * @returns {string} zi
   */
  getOrGenerateZI() {
    if (utils.getType(this._zi) !== "string" || utils.isNullOrEmpty(this._zi)) {
      return utils.getUUIDv4();
    }
    this.logger.logInfo("getOrGenerateZI", "ZI cookie value: ", this._zi);
    return this._zi;
  }

  /**
   * set given value for zi in storage if it has consent
   */
  setZI(value) {
    this.logger.logInfo("setZI", "ZI cookie being set as: ", value);
    this._zi = value;
    this.saveZItoStorage();
  }

  /**
   * reset zi and store it in storage
   */
  resetZI() {
    this._zi = utils.getUUIDv4();
    this.logger.logInfo("resetZI", "ZI cookie being reset as: ", this._zi);
    this.saveZItoStorage();
  }

  /**
   * check if zs is present ( if not generate a guuid) and return the same
   *
   * @returns {string} zs
   */
  getOrGenerateZS() {
    if (utils.getType(this._zs) !== "string" || utils.isNullOrEmpty(this._zs)) {
      this._zs = utils.getUUIDv4();
    }
    return this._zs;
  }

  /**
   * check if ga client id is allowed and ga client id is present then return the same
   * @returns {string} Google Analytics Client ID Cookie Value
   */
  getGAClientIdFromStorage() {
    if (this.optionsService.options.allowGAClientId) {
      let gaClientId = this.storageService.get(
        this.optionsService.options.gaClientIdCookiePrefix
          ? this.optionsService.options.gaClientIdCookiePrefix +
              GA_COOKIE_SUFFIX
          : GA_COOKIE_SUFFIX,
        GA_COOKIE_CONFIG
      );
      if (gaClientId) {
        gaClientId = gaClientId.split(".").splice(2).join(".");
      }
      this.logger.logInfo(
        "getGAClientIdFromStorage",
        "Google Analytics Client ID: ",
        gaClientId
      );
      return gaClientId;
    }
    return null;
  }

  /**
   * check if ga user id is allowed and ga user id is present then update the same
   * @returns {string} Google Analytics User ID Cookie Value
   */
  setGAUserIdFromStorage() {
    let id;
    if (this.optionsService.options.gaUserIdCookieName) {
      id = this.storageService.get(
        this.optionsService.options.gaUserIdCookieName,
        GA_COOKIE_CONFIG
      );
      this.logger.logInfo(
        "setGAUserIdFromStorage",
        "Google Analytics User ID: ",
        id
      );
    }
    this._gaUserId = id;
  }

  /**
   * reset zs and store it in storage
   */
  resetZS() {
    this._zs = utils.getUUIDv4();
    this.logger.logInfo("resetZS", "Setting ZS as: ", this._zs);
    this.saveZStoStorage();
  }

  resetZsExpiry() {
    this.loadZSfromStorage();
    this.getOrGenerateZS();
    this.saveZStoStorage();
  }

  /**
   * function to load ZI from storage
   */
  loadZIfromStorage() {
    var zi = this.storageService.get(
      getZIKey(this.optionsService.options.apiKey)
    );
    this._zi = utils.isEmptyString(zi) ? null : zi;
    this.saveZItoStorage();
  }

  /**
   * function to save ZI from storage
   */
  saveZItoStorage() {
    if (utils.isEmptyString(this._zi)) return;
    this.logger.logInfo("saveZItoStorage", "Saving ZI to storage: ", this._zi);
    this.storageService.set(
      getZIKey(this.optionsService.options.apiKey),
      this._zi,
      { expirationDays: 365 }
    );
    this._identityEvents.trigger(this.getAllIdentites());
  }

  /**
   * function to load the identity object saved in the storage.
   */
  loadUserIdentityFromStorage() {
    var identity = this.storageService.getFromCookieOrSession(
      getIdentityKey(this.optionsService.options.apiKey),
      { isString: false, hash: false }
    );
    this._identity = { ...identity };
    this.logger.logInfo(
      "loadUserIdentityFromStorage",
      "User Identity: ",
      this._identity
    );
    this.saveUserIdentityToStorage();
  }

  /**
   * function to save the identity object saved in the storage.
   */
  saveUserIdentityToStorage() {
    const identityName = getIdentityKey(this.optionsService.options.apiKey);
    this.logger.logInfo(
      "saveUserIdentityToStorage",
      "Saving User Identity to sesion storage with name as: ",
      identityName
    );
    const identityStoredOutsideSessionStorage =
      this.storageService.get(identityName);
    if (identityStoredOutsideSessionStorage) {
      this._identity = {
        ...this._identity,
        ...identityStoredOutsideSessionStorage,
      };
      this.storageService.removeFromCookieOrSession(identityName);
    }
    this.storageService.setInCookieOrSession(identityName, this._identity, {
      hash: false,
      isString: false,
    });
    this._identityEvents.trigger(this.getAllIdentites());
  }

  /**
   * function to load ZS cookie from storage
   */
  loadZSfromStorage() {
    var zs = this.storageService.getFromCookieOrSession(
      getZSKey(this.optionsService.options.apiKey),
      { isString: true }
    );
    this._zs = utils.isEmptyString(zs) ? utils.getUUIDv4() : zs;
    this.saveZStoStorage();
  }

  /**
   * function to save ZS from storage
   */
  saveZStoStorage() {
    if (utils.isEmptyString(this._zs)) return;
    this.logger.logInfo("saveZStoStorage", "Saving ZS to storage: ", this._zs);
    this.storageService.removeFromCookieOrSession(
      getZSKey(this.optionsService.options.apiKey)
    );
    this.storageService.setInCookieOrSession(
      getZSKey(this.optionsService.options.apiKey),
      this._zs,
      { isString: true, hash: false }
    );
    this._identityEvents.trigger(this.getAllIdentites());
  }

  getIdentity() {
    return this._identity;
  }

  /**
   * set the Identities as Identity property and save to storage
   *
   * @param {Identity} identity - hashed values of the identity to be saved
   */
  setIdentity(identity = {}) {
    // hash and save the identity to userProperties
    this._identity = { ...this._identity, ...identity };
    this.logger.logInfo("setIdentity", "Set identity as: ", this._identity);
    this.saveUserIdentityToStorage();
  }

  /**
   * unset the Identities set using [setIdentites]{@link setUserIdentities}
   */
  unsetIdentity() {
    this.logger.logInfo("unsetIdentity", "Unsetting identity");
    if (arguments.length === 0) {
      // clear all identity
      this._identity = {};
      this.saveUserIdentityToStorage();
      return;
    }
    var unsetIdentities = arguments[0];
    if (utils.getType(unsetIdentities) === "array") {
      // clear only mention identities
      this._identity = utils.removePropertiesfromObject(
        this._identity,
        unsetIdentities
      );
      this.saveUserIdentityToStorage();
      return;
    }
  }
  resolveAll() {
    this.logger.logInfo("resolveAll", "Resolve all identifiers.");
    this._zi = this.getOrGenerateZI();
    this._zs = this.getOrGenerateZS();
    this._gaClientId = this.getGAClientIdFromStorage();
    this.saveZItoStorage();
    this.saveUserIdentityToStorage();
    this.saveZStoStorage();
    if (!this.optionsService.options.gaUserIdOnlyLoginEvent) {
      this.setGAUserIdFromStorage();
    }
  }

  resolveAndGetZI() {
    this._zi = this.getOrGenerateZI();
    this.saveZItoStorage();
    return this._zi;
  }

  resolveAndGetZS() {
    this._zs = this.getOrGenerateZS();
    this.saveZStoStorage();
    return this._zs;
  }

  resolveAndGetGAClientID() {
    this._gaClientId = this.getGAClientIdFromStorage();
    return this._gaClientId;
  }

  resolveAndgetGAUserIdFromStorage() {
    if (!this.optionsService.options.gaUserIdOnlyLoginEvent) {
      this.setGAUserIdFromStorage();
    }
    return this._gaUserId;
  }

  clearAll() {
    this.logger.logInfo("clearAll", "Clear all identifiers.");
    this._zi = null;
    this._identity = {};
    this._zs = null;
    this._gaClientId = null;
    this._gaUserId = null;
    this.storageService.remove(getZIKey(this.optionsService.options.apiKey));
    this.storageService.removeFromCookieOrSession(
      getIdentityKey(this.optionsService.options.apiKey)
    );
    this.storageService.removeFromCookieOrSession(
      getZSKey(this.optionsService.options.apiKey)
    );
  }

  /**
   * load all from storage
   */
  loadFromStorage() {
    this.loadZIfromStorage();
    this.loadUserIdentityFromStorage();
    this.loadZSfromStorage();
    this._gaClientId = this.getGAClientIdFromStorage();
    if (!this.optionsService.options.gaUserIdOnlyLoginEvent) {
      this.setGAUserIdFromStorage();
    }
  }

  get events() {
    return this._identityEvents;
  }

  getAllIdentites() {
    return {
      user_zi: this._zi,
      // user_zs: this._zs, // this is not an identity ,instead its an event attribute
      ...this._identity,
    };
  }
}
