import utils from "../utils";

/**
 * Represents a PageService.
 */
export class PageService {
  constructor() {
    this.pageProperties = {};
  }

  /**
   * Returns the page properties.
   *
   * @returns {object} The page properties.
   */
  getPageProperties() {
    var location = utils.getLocation();
    var properties = this.pageProperties[location.pathname]
      ? this.pageProperties[location.pathname]
      : {};
    return {
      page_path: location.pathname,
      page_referrer: utils.getReferrer(),
      ...properties,
    };
  }

  /**
   * Sets the page properties.
   *
   * @param {object} properties - The properties to set.
   */
  setPageProperties(properties = {}) {
    var pathName = utils.getLocation().pathname;
    var setProperties = utils.validateProperties(properties);
    if (!utils.isEmptyString(pathName))
      this.pageProperties[pathName] = setProperties;
  }
}
