import queryString from "query-string";

/**
 * @typedef RequestData
 * @type {object}
 * @property {object} params
 * @property {object} body
 */
/**
 * @typedef Headers
 * @type {object}
 * @property {string} contentType
 * @property {string} basicAuthKey - key that should be added with Basic Suffix in the Authourization Key
 */

/**
 * This callback is called when a request is completed
 * @callback requestCallback
 * @param {number} status
 * @param {string} responseMessage
 */

export class RequestService {
  constructor() {}
  getAuthorization(key) {
    return `Basic ${key}`;
  }

  getHeaders(headers) {
    return headers
      ? {
          ...(!!headers.contentType
            ? { "Content-Type": headers.contentType }
            : {}),
          ...(!!headers.basicAuthKey
            ? { Authorization: this.getAuthorization(headers.basicAuthKey) }
            : {}),
        }
      : {};
  }
  /**
   *
   * @param {string} url
   * @param {object} params
   * @param {requestCallback} requestCallback
   * @param {Headers} headers
   */
  get(url, params, requestCallback, headers = null) {
    var _urlParams = queryString.stringifyUrl({ url: url, query: params });
    var r = new Request(_urlParams, {}, { headers: this.getHeaders(headers) });
    r.send(requestCallback);
  }
  /**
   *
   * @param {string} url
   * @param {object} data
   * @param {requestCallback} requestCallback
   * @param {Headers} headers
   */
  post(url, data, headers = {}, requestCallback) {
    var _urlParams = queryString.stringifyUrl({
      url: url,
      query: !!data.params ? data.params : {},
    });
    var r = new Request(_urlParams, JSON.stringify(data.body), {
      method: "POST",
      headers: this.getHeaders({
        contentType: "application/json;charset=UTF-8",
        ...headers,
      }),
    });
    r.send(requestCallback);
  }
}

/*
 * Simple AJAX request object
 */
export const Request = function (url, data = {}, options = {}) {
  this.url = url;
  this.data = data;
  this.options = options;
};

Request.prototype.send = function (callback) {
  var isIE = window.XDomainRequest ? true : false;
  if (isIE) {
    var xdr = new window.XDomainRequest();
    xhr.open(this.options.method || "GET", this.url, true);
    if (!!this.options.headers) {
      Object.keys(this.options.headers).forEach((key) => {
        xdr.setRequestHeader(key, this.options.headers[key]);
      });
    }
    xdr.onload = function () {
      callback(200, xdr.responseText);
    };
    xdr.onerror = function () {
      // status code not available from xdr, try string matching on responseText
      callback(500, xdr.responseText);
    };
    xdr.withCredentials = true;
    xdr.ontimeout = function () {};
    xdr.onprogress = function () {};
    xdr.send(queryString.stringify(this.data));
  } else {
    let xhr = new XMLHttpRequest();
    xhr.withCredentials = true;
    xhr.open(this.options.method || "GET", this.url);
    if (!!this.options.headers) {
      Object.keys(this.options.headers).forEach((key) => {
        xhr.setRequestHeader(key, this.options.headers[key]);
      });
    }
    xhr.onload = () => {
      if (xhr.status >= 200 && xhr.status < 300) {
        callback(xhr.status, xhr.response);
      } else {
        callback(xhr.status, xhr.statusText);
      }
    };
    xhr.onerror = () => callback(xhr.status, xhr.statusText);
    xhr.send(this.data);
  }
};
