// import log from 'loglevel';
import { OptionsService } from './options';
import log from 'loglevel';

export const logLevels = ['trace', 'debug', 'info', 'warn', 'error'];
/**
 * @module logger
 * @class - A logging service built with loglevel library
 */
export class LoggerService {
  
  constructor(mLog = log) {
    this.log = mLog;
  }

  updateLogger(enableLogging = false, logLevel = ""){
    
    const queryString = window.location.search;
    const regex = /[?&]zeo_debug=([^&]*)/;

    const match = queryString.match(regex);
    let zeoDebugValue = false;

    if (match && match[1] !== undefined) {
      zeoDebugValue = match[1];
    } 

    if (enableLogging || zeoDebugValue) {
      this.log.enableAll();
      if (logLevels.find(l => l === logLevel)) {
        this.log.setLevel(logLevel);
      } else {
        this.log.setLevel('info');
      }
    } else {
      this.log.disableAll();
    }
  }


  setLoggerStatus(enable, logLevel = 'debug') {
    if (enable) {
      this.log.enableAll();
      if (logLevels.find(l => l === logLevel)) {
        this.log.setLevel(logLevel);
      } else {
        this.log.setLevel('warn');
      }
    } else {
      this.log.disableAll();
    }
  }

  logInfo(methodName, message, ...args) {
    const styledMessage = `%c[${methodName}]:: ${message}`;
    const logStyle = 'color: green; font-weight: bold';

    this.log.info(styledMessage, logStyle, ...args);
  }

  logWarning(methodName, message, ...args) {
    const styledMessage = `%c[${methodName}]:: ${message}`;
    const logStyle = 'color: red; font-weight: bold';

    this.log.warn(styledMessage, logStyle, ...args);
  }

  logError(methodName, message, ...args) {
    const styledMessage = `%c[${methodName}]:: ${message}`;
    const logStyle = 'color: pruple; font-weight: bold';

    this.log.error(styledMessage, logStyle, ...args);
  }
}