function checkAndWaitForZeotapSDK(callback, window) {
  if (!!window.zeotap) {
    // zeotap tag is set
    if (window.zeotap.scriptLoaded) {
      // zeotap sdk is loaded
      callback(window);
      return;
    }

    window.addEventListener("zeotapLoaded", (event) => {
      callback(window);
      window.removeEventListener("zeotapLoaded", this);
    });

    return;
  }
  callback(window);
}

function getWriteKeyFromZeotapSDK() {
  return window.zeotap.getWriteKey();
}

export default function initInteract(
  window,
  interactConnect,
  prop = "zeotapInteract",
  writable = false
) {
  var old = window[prop] || {};
  var zeotap = interactConnect;
  zeotap._q = old._q || [];

  if (zeotap._q.length && zeotap._q[0].length) {
    const firstMethodName = zeotap._q[0][0];
    const firstMethodArgs = zeotap._q[0][1];
    if (firstMethodName === "init") {
      const initMethodMeta = zeotap._q.shift();
      if (!initMethodMeta[1] || initMethodMeta[1] === undefined) {
        zeotap.init(getWriteKeyFromZeotapSDK(window));
      } else {
        zeotap.init(initMethodMeta[1]);
      }
    } else if (firstMethodName === "callMethod" && firstMethodArgs === "init") {
      const initMethodMeta = zeotap._q.shift();
      zeotap.callMethod("init", initMethodMeta[2]);
    }
  }

  Object.defineProperty(window, prop, {
    value: Object.seal(zeotap),
    writable,
  });
}
var logger = new LoggerService(log);

const getIdentityService = (window, optionsService, storageService, logger) => {
  if (window.zeotap) {
    // then the collect sdk on the page will handle any identities lifestyle
    logger.logInfo(
      "getIdentityService",
      "Using zeotap collect sdk identity service"
    );
    return {
      getAllIdentites: window.zeotap.getAllIdentites,
      events: new IdentityEvents(),
      getIdentity: (...args) => {},
      setIdentity: (...args) => {},
      resolveAll: (...args) => {},
      loadFromStorage: (...args) => {},
    };
  }
  return new IdentityService(optionsService, storageService, logger);
};

checkAndWaitForZeotapSDK((window) => {
  var eventTarget = new EventTarget();
  var optionsService = new OptionsService(eventTarget);

  var pageService = new PageService();

  var storageService = new StorageService(
    optionsService,
    new cookieStorage(),
    localStorage
  );

  var identityService = getIdentityService(
    window,
    optionsService,
    storageService,
    logger
  );

  var interactionsService = new InteractionsService(
    optionsService,
    logger,
    new RequestService(),
    pageService,
    window,
    identityService
  );
  var interactClient = new ZeotapInteractionsClient(
    optionsService,
    logger,
    interactionsService,
    identityService
  );
  var interact = new InteractConnect({
    client: interactClient,
    optionsService,
    logger,
  });
  initInteract(window, interact);
  // firing identities event in case any identities event
}, window);
