import { ZeotapIDPClient } from "../client/idpClient";
import { ZeotapInteractionsClient } from "../client/interact";
import { ConsentService } from "../consent/consent";
import { SDK_EVENTS } from "../constant";
import { Event, EventTarget } from "../events/eventTarget";
import { LoggerService } from "../logger";
import { Identities } from "../model";
import { OptionsService } from "../options";
import utils from "../utils";

/**
 * @typedef {object} InteractConnectConfig
 * @property {ZeotapInteractionsClient} client
 * @property {OptionsService} optionsService
 * @property {LoggerService} logger
 *
 */

/**
 * @module zeotap
 */
/**
 *
 *
 * @class InteractConnect - A class to connect page to Interact client
 */

export class InteractConnect {
  /**
   * @constructor
   *
   * @param {InteractConnectConfig} config
   *
   */
  constructor({ client, optionsService, logger, isRunningQ = false }) {
    this._q = [];
    this._qcmp = [];
    var isReady = () => {
      return !!client;
    };
    var queueIfNotReady = (functionName, args) => {
      this._q.push([functionName].concat(args));
    };
    this.init = function init(...args) {
      var options;
      if (args.length === 0) return;
      else if (args.length === 1 && utils.getType(args[0]) === "object")
        options = args[0];
      else {
        options = { apiKey: args[0], ...args[1] };
      }
      // set options
      optionsService.setOptions(options);
      logger.logInfo("InteractConnect initialized with options", options);
      this.runQueuedFunctions();
      client // for now consent is ignored
        .run();
    }.bind(this);
    this.runQueuedFunctions = function () {
      if (isRunningQ) return;
      isRunningQ = true;
      for (var i = 0; i < this._q.length; i++) {
        var callStack = this._q[i] || [];
        var _fn =
          !!callStack && callStack.length > 0 && !!callStack[0]
            ? this[callStack[0]]
            : () => {};
        if (typeof _fn === "function") {
          var fn = _fn;
          fn.apply(this, callStack.slice(1));
        }
      }
      this._q = []; // clear function queue after running
      isRunningQ = false;
    }.bind(this);

    this.callMethod = function callMethod() {
      var callStack = arguments; // this includes the function name and the var its called with
      var _fn =
        !!callStack && callStack.length > 0 && !!callStack[0]
          ? this[callStack[0]]
          : () => {};
      // getting the function of the following name or calling empty method
      if (typeof _fn === "function") {
        var fn = _fn;
        fn.apply(this, Array.prototype.slice.call(callStack, 1));
      }
    }.bind(this);

    this.setUserIdentities = function (...args) {
      if (!isReady()) {
        queueIfNotReady(
          "setUserIdentities",
          Array.prototype.slice.call(arguments, 0)
        );
        return;
      }
      client.setUserIdentities(...args);
    }.bind(this);

    this.getUserIdentities = function (...args) {
      return client.getUserIdentities(...args);
    };
  }

  /**
   * Method called to initialize the IDP script
   *
   * @alias module:zeotap#init
   *
   * @param {Options} options
   */
  init(options) {}

  /**
   *
   * Method to send userIdentities[ Email or phone number only!] and drop an IDP cookie for the given partnerId
   *
   * @alias module:zeotap#setUserIdentities
   *
   * @param {Identities} properties
   * @param {boolean} [isHashed=false] - if true , the properties sent are already hashed
   */
  setUserIdentities(properties, isHashed = false) {}

  /**
   * Method to get the userIdentities stored in the client
   */
  getUserIdentities() {}

  /**
   *
   * A common interface to call the methods of zeotap object
   *
   * @alias module:zeotap#callMethod
   *
   * @public
   *
   *
   * @public
   * @param {string} functionName - name of the method to be called
   * @param {...*} functionParams - parameters to be passed for the function mentioned in the first param
   */
  callMethod() {}
}
