export const flattenObject = (obj, prefix = "", seperator = "_") => {
  return Object.keys(obj).reduce((acc, k) => {
    const pre = prefix.length ? prefix + seperator : "";
    if (
      typeof obj[k] === "object" &&
      obj[k] !== null &&
      !Array.isArray(obj[k])
    ) {
      Object.assign(acc, flattenObject(obj[k], pre + k));
    } else {
      acc[pre + k] = obj[k];
    }
    return acc;
  }, {});
};
