export class InteractionLoggingService {
  /**
   * Creates an instance of InteractionsService.
   * @param {OptionsService} optionsService - The options service.
   * @param {LoggerService} logger - The logger.
   * @param {RequestService} requestService - The request service.
   */
  constructor(optionsService, logger, requestService) {
    this.requestService = requestService;
    this.optionsService = optionsService;
  }
  callInteractionPixel(interaction, field = "pixel") {
    const pixel = interaction[field];
    if (!pixel) return;
    this.requestService.get(pixel, {}, (status, response) => {}, {
      basicAuthKey: btoa("w_k:" + this.optionsService.options.apiKey),
    });
  }
}
