import utils from '../utils';
import _cookie from './basicCookie';
 
const TEST_COOKIE ="ztestc";
const defaultOptions = {
  expirationDays: undefined,
  domain: undefined,
  ignoreSuffix:false, 
}

export default (_win,doc)=>{

var cookieStorage =  function cookieStorage(){
    this._options = {...defaultOptions}
}

var cookie = _cookie(doc);

const getLocation = () => {
    return _win.location;
  };
  
cookieStorage.prototype.reset = function() {
  this._options = {...defaultOptions};
};

const getHost = (url) => {
  const a = doc.createElement('a');
  a.href = url;
  return a.hostname || getLocation().hostname; 
};
const topDomain = (url) => {
  const host = getHost(url);
  const parts = host.split('.');
  const last = parts[parts.length - 1];
  const levels = [];

  if (parts.length === 4 && last === parseInt(last, 10)) {
    return levels;
  }

  if (parts.length <=1) {
    return levels;
  }

  for (let i = parts.length - 2; i >= 0; --i) {
    levels.push(parts.slice(i).join('.'));
  }

  for (let i = 0; i < levels.length; ++i) {
    const cname = '__tld_test__';
    const domain = levels[i];
    const opts = { domain: '.' + domain };
    cookie.setCookie(cname, 1, opts);
    if (cookie.getCookie(cname)) {
        cookie.setCookie(cname, null, opts);
      return domain;
    }
  }
  return '';
};


cookieStorage.prototype.getTld = function getTld(){
  return '.' + topDomain(getLocation().href);
}


cookieStorage.prototype.getValidDomain = function getValidDomain(opts){
  var domain = (!utils.isEmptyString(opts.domain)) ? opts.domain : this.getTld() ;
  opts.domain = domain;
  var token = Math.random();
  // set Test cookie 
  this.set(TEST_COOKIE, token,opts);
  var stored = this.get(TEST_COOKIE,opts);
  if (!stored || stored !== token) {
      // if access not found setting to null
    domain = null;
  }
  this.remove(TEST_COOKIE,opts);
  return domain;
}

// untill set otherwise this should set the domain as .maindomain.com

cookieStorage.prototype.options = function options(opts) {
  if (arguments.length === 0) {
    return this._options;
  }

  opts = opts || {};

  this._options.expirationDays = opts.expirationDays;
  this._options.secure = opts.secure;
  this._options.ignoreSuffix = !!opts.ignoreSuffix?opts.ignoreSuffix:false;
  this._options.domain = opts.domain
  this._options.domain = this.getValidDomain(this._options);  
  return this._options;
};

/**
 * @private
 * Cases : No options set returns name as is 
 * Options set with domain returns namedomain.com this prevents from two cookies from getting mixed 
 * @param {String} name 
 */
var _domainSpecific = function(name,_options=defaultOptions) {
  // differentiate between cookies on different domains
  var suffix = '';
  if (!_options.ignoreSuffix && !!_options.domain) {
    suffix = _options.domain.charAt(0) === '.' ? _options.domain.substring(1) : _options.domain;
  }
  return name + suffix;
};


cookieStorage.prototype.get = function get(name,overrideOptions={}) {
  var options =  {...this._options,...overrideOptions}
  const value = cookie.getCookie(_domainSpecific(name,options) + '=');
  try {
    if (value) {
      if (overrideOptions.encrypted !== false) {
        return JSON.parse(utils.base64.decode(value));
      } else {
        return value;
      }
    }
  } catch (e) {
    return null;
  }

  return null;
};


cookieStorage.prototype.set = function set(name, value,overrideOptions={}) {
  var options =  {...this._options,...overrideOptions}
  options.expirationDateTime = options.expirationDateTime ? options.expirationDateTime : ( options.expirationDays ? new Date().getTime() + options.expirationDays * 24 * 60 * 60 * 1000: undefined);
  try {
    cookie.setCookie(_domainSpecific(name,options), utils.base64.encode(JSON.stringify(value)),options);
    return true;
  } catch (e) {
    return false;
  }
};


cookieStorage.prototype.remove = function(name,overrideOptions={}) {
  var options =  {...this._options,...overrideOptions}
  try {
    cookie.setCookie(_domainSpecific(name,options), null, options);
    return true;
  } catch (e) {
    return false;
  }
};

return cookieStorage;
};
