import sha1 from 'crypto-js/sha1';
import sha256 from 'crypto-js/sha256';
import md5 from 'crypto-js/md5';

/**
 * @private
 * This is a constructor method that returns an object that can encrypt strings in md5,SHA-256,SHA-1
 */
var Encrypt = function Encrypt(value){
    this.value = typeof value !== 'string' ? `${value}` : value.trim()
}

Encrypt.prototype.sha256 = function sha256x(){
    return sha256(this.value).toString();
}
Encrypt.prototype.sha1 = function sha1x(){
    return sha1(this.value).toString();
}
Encrypt.prototype.md5 = function md5x(){
    return md5(this.value).toString();
}
Encrypt.prototype.sha256u = function sha256u(){
    return sha256(this.value.toUpperCase()).toString();
}
Encrypt.prototype.sha256l = function sha256l(){
    return sha256(this.value.toLowerCase()).toString();
}
Encrypt.prototype.sha1u = function sha1u(){
    return sha1(this.value.toUpperCase()).toString();
}
