import { DEFAULT_OPTIONS } from "./constant";
import { EventTarget } from './events/eventTarget';
import { Options } from "./model";
import utils from './utils';
const TOPIC_OPTIONS_CHANGE = "optionsChange"


/**
 * @class
 * @property {number} [expirationDays=undefined] - days if expiration. Only replevant to cookies, no expiration for local Storage.
 * @property {string} [domain=undefined] if specified cookies will be stored against this subdomain(if possible) or fall back to hostname
 * @property {boolean} [secure=false] if set to true all cookies are set with Secure Tag. @see {@link https://developer.mozilla.org/en-US/docs/Web/HTTP/Cookies}
 * @property {number} expirationDateTime - the exact date ( in EPOCH time ) till when the cookie should live in a browser. ( The actual implementation of cookie expiry varies from browser to browser)
 */
export class StorageOptions { }

/**
 * @class
 * @property {Options}  
 */
export class OptionsService {
  /**
   * @param {EventTarget} eventTarget - event target for event sourcing
   */
  constructor(
    eventTarget = new EventTarget()
  ) {
    this._options = { ...DEFAULT_OPTIONS };
    this.onOptionsChangeEvent = eventTarget;
  }
  set apiKey(key) {
    this._options.apiKey = key;
  }
  get apiKey() {
    this._options.apiKey;
  }

  /**
   * set Options
   * @param {Options} options
   */
  set options(options) {
    this._options = {
      ...this._options,
      ...options,
    }
  }
  /**
   * @returns {Options}
   */
  get options() {
    return this._options;
  }

  get persistentStorageKeySuffix() {
    return "_" + utils.base64.encode(this._options.apiKey);
  }
  set domain(domain) {
    this._options.domain = domain;
  }

  validateOptions(options) {

    // validate user identity hashing configurations
    if (options.areIdentitiesHashed && options.hashIdentities) {
      console.error('Cannot transform hashed identities.');
    }
  }

  setOptions(options) {
    this.validateOptions(options);
    if (typeof options === 'object') {
      this._options = {
        ...this._options,
        ...options ? options : {},
      }
      this.onOptionsChangeEvent.dispatchEvent(new Event(TOPIC_OPTIONS_CHANGE, this._options))
    }
    }
  /**
   * returns the storage options from the user set options
   * @returns {StorageOptions} 
   */
  getStorageOptions() {
    return {
      expirationDays: this._options.storageExpirationDays,
      domain: this._options.domain,
      secure: this._options.onlySecureCookie,
      persistenceInCookieStorage: this._options.persistenceInCookieStorage
    }
  }

  listenToOptionsChange(callback) {
    /**
     * @todo - change name and usages to listenToOptionsSet  
     */
    /**
     * Next line makes the callback func runs immediately on registering listenToOptionsChange.
     * Ideally this is not expected to run on callback registration.
     *
     *  But this is req as analytics.js needs to load identityService and IDPService from storage on first instance itself.
     * 
     *   this.optionsService.listenToOptionsChange((options) => {
     *    if (!!this.optionsService.options.apiKey) {
     *    this.identityService.loadFromStorage();
     *    this.idpService.loadIDPfromStorage();
     *   }
     * 
     * need to remove this callback dependency to load identityService and idpService on first instance/ on ZeotapAnalyticsClient object creation
     */

    callback(new Event(TOPIC_OPTIONS_CHANGE, this._options))  
    this.onOptionsChangeEvent.addEventListener(TOPIC_OPTIONS_CHANGE, callback);
  }
  removeOptionsChangeListener(callback) {
    this.onOptionsChangeEvent.removeEventListener(TOPIC_OPTIONS_CHANGE, callback)
  }
}