import { CONSENT_IDENTIFY, CONSENT_TRACK } from "../consent/consent";
import { SPL_EVENT_NAMES } from "../constant";
import { IdentityService } from "../ID/identity";
import { InteractionsService } from "../interactions/interactions";
import { LoggerService } from "../logger";
import { OptionsService } from "../options";
import { PageService } from "../page/page";
import { StorageService } from "../storage/storage";
import utils from "../utils";

/**
 * Represents an interaction in the SDK.
 * @typedef {Object} SDKInteraction
 * @property {string} type - The type of interaction ('event' or 'function').
 * @property {Object}  dataToSendConfigDetails- The parameters for data interaction (only applicable for 'data' type).
 * @property {Object}  displayConfigDetails - the parameters for display interactions (only applicable for 'display' type).
 */

/**
 * Represents a client for Zeotap interactions.
 */
export class ZeotapInteractionsClient {
  /**
   *
   * @param {Window} window - The window object.
   * @param {OptionsService} optionsService - The options service handler.
   * @param {LoggerService} logger - The logger service handler.
   * @param {InteractionsService} interactionsService - The interactions service handler.
   * @param {StorageService} storageService - The storage service handler.
   * @param {PageService} pageService - The page service handler.
   * @param {IdentityService} identityService - The identity service handler.
   */
  constructor(optionsService, logger, interactionsService, identityService) {
    this.optionsService = optionsService;
    this.logger = logger;
    this.interactionsService = interactionsService;
    this.identityService = identityService;

    this.optionsService.listenToOptionsChange((options) => {
      if (!!this.optionsService.options.apiKey) {
        this.identityService.loadFromStorage();
        this.identityService.events.reset();
        this.listenToIdentityChange();
        this.identityService.events.trigger(
          this.identityService.getAllIdentites()
        );
      }
    });
  }

  listenToIdentityChange() {
    this.identityService.events.listen(() => {
      this.onIdentityChange();
    });
  }

  onIdentityChange() {
    this.interactionsService.resolveInteractions();
  }

  /**
   * Runs the Zeotap interactions client.
   */
  run() {
    this.resolveIdentifiers();
  }

  /**
   * This function read or generates the ids if not already created and also syncs cookies on the SPL domain with those ids.
   * See more [Understanding SPL Cookies]{@link .}
   */
  resolveIdentifiers() {
    this.identityService.resolveAll();
  }

  transformIdentitiesBasedOnConfiguration(identities, isHashed) {
    // backward compatibility => when areIdentitiesHashed, hashIdentities are not set by existing clients
    this.logger.logInfo(
      "transformIdentitiesBasedOnConfiguration",
      "Transforming identities based on Configuration: ",
      identities
    );

    if (this.optionsService.options.areIdentitiesHashed === undefined) {
      if (isHashed) {
        return utils.validateHashedIdentities(identities);
      } else {
        return utils.hashIdentities(
          utils.validateRawIdentities(identities, true)
        );
      }
    } else if (this.optionsService.options.areIdentitiesHashed === false) {
      return this.optionsService.options.hashIdentities
        ? utils.hashIdentities(utils.validateRawIdentities(identities, true))
        : utils.validateRawIdentities(identities);
    } else {
      return utils.validateHashedIdentities(identities);
    }
  }

  /**
   *
   * An object that should only have email,cellno and loginid as property.
   *
   * @param {Identities} identities
   */
  setUserIdentities(identities = {}, isHashed = undefined) {
    const existingIds = this.identityService.getIdentity();
    // transform identities based on hashing configurations
    const finalIdentities = this.transformIdentitiesBasedOnConfiguration(
      { ...existingIds, ...identities },
      isHashed
    );
    this.identityService.setIdentity(finalIdentities);
    // Also we want to resolve interactions once id is set
    this.interactionsService.resolveInteractions();
  }

  getUserIdentities() {
    return this.identityService.getIdentity();
  }
}
