import utils from "../utils";

export default function localStorage(_window) {
  var _localStorage = ((_win) => {
    if (!_win.localStorage) {
      return {
        length: 0,
        setItem: function (k, v) {},
        getItem: function (k) {},
        removeItem: function (k) {},
        clear: function () {},
        key: function (k) {},
      };
    }
    return _win.localStorage;
  })(_window);
  var _encoding = "base64"; // or utf-8
  var isBase64 = () => {
    return _encoding === "base64";
  }; // by default we want to encode the data in base64

  return {
    setEncoding: function setEncoding(encoding) {
      _encoding = encoding;
    },

    options: (_opts) => {
      // do nothing as local storage does not care for this
      return _opts;
    },
    get: function get(name) {
      try {
        const val = _localStorage.getItem(name);
        if (!!val) {
          if (isBase64()) {
            return JSON.parse(utils.base64.decode(val));
          } else {
            return JSON.parse(val);
          }
        }
        return null;
      } catch (e) {
        console.error("Error occured in getting from local storage", name, e);
      }
      return null;
    },
    set: function set(name, value) {
      try {
        if (isBase64()) {
          _localStorage.setItem(
            name,
            utils.base64.encode(JSON.stringify(value))
          );
        } else {
          _localStorage.setItem(name, JSON.stringify(value));
        }
        return true;
      } catch (e) {
        console.error(
          "Error occured in setting to local storage",
          name,
          value,
          e
        );
      }
      return false;
    },
    remove: function (name) {
      try {
        _localStorage.removeItem(name);
      } catch (e) {
        return false;
      }
    },
  };
}
